import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReactHtmlParser from 'react-html-parser';

class SelectedFeed extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        feedData: [],
        selectedFeed: []
    };

    onLoadFeedList = () => {
        const { filterList } = this.props.feedData;
        this.setState((prevState) => ({ ...prevState, feedData: filterList }));
    }

    handleOnChange = (feedId) => {
        const { updateFeedList } = this.props;
        const { selectedFeed } = this.state

        if (selectedFeed.indexOf(feedId) !== -1) {
            var list = selectedFeed.filter((feed) => feed !== feedId);
            this.setState((prevState) => ({ ...prevState, selectedFeed: list }), () => {
                updateFeedList(this.state.selectedFeed);
            });
        }
        else {

            this.setState((prevState) => ({ ...prevState, selectedFeed: [...this.state.selectedFeed, feedId] }), () => {
                updateFeedList(this.state.selectedFeed);
            });
        }
    }

    componentDidMount() {
        this.onLoadFeedList()
    }
    render() {
        const { feedData } = this.state
        return <div className="row gx-3">
            {feedData && Object.keys(feedData).length > 0 && feedData.feeds.length > 0 ? feedData.feeds.map((item) => {
                return item.name ? <div key={item.feedId} className="col-sm-6 col-lg-4">
                    <div className="d-flex align-items-start justify-content-between mb-3">
                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1 overflow-hidden w-100 pe-2">
                            <input className="form-check-input" type="checkbox" name={item.feedId} value={item.feedId} onChange={() => this.handleOnChange(item.feedId)} checked={this.props.selectedFeedsList.indexOf(item.feedId) !== -1} />
                            <span className="form-check-label text-break">{ReactHtmlParser(item.name)}</span>
                        </label>
                        <span className="badge text-muted border rounded-4 min-w-30px fs-9">{item.count}</span>
                    </div>
                </div> : null
            }) : null
            }

        </div>

    }
}

const mapStateToProps = state => {
    return {
        feedData: state.feedData
    }
}

export default connect(mapStateToProps)(SelectedFeed);