import React, { Component } from "react";
import {
  Modal,
  Dropdown,
  InputGroup,
  DropdownButton,
  Form,
  Collapse,
} from "react-bootstrap";
import CreatableSelect from "react-select/creatable";
import SelectedNetwork from "./SelectedNetwork";
import SelectedFeed from "./SelectedFeed";
import ChooseCollection from "../../MyAssets/AddAssetsModal/Compontnets/Elements/ChooseCollection";
import { connect } from "react-redux";
import { addRule } from "../../../../Services/Actions/rulesAction";

const byHandle = () => {
  return <i className="ri-at-line icon-5"></i>;
}
const byHashtag = () => {
  return <i className="ri-hashtag icon-5"></i>;
}
const byWord = () => {
  return <i className="ri-text icon-5"></i>;
}
const byTag = () => {
  return <i className="ri-price-tag-3-line icon-5"></i>;
}
const initialState = {
  ruleOn: 1,
  advanceOption: 1,
  name: "",
  action: "Copy",
  text: "",
  tag: [],
  collectionId: 0,
  selectedNetworkList: [],
  selectedFeedsList: [],
  validateName: false,
  validateCollection: false,
  validateAction: false,
  validateAdvOpt: false,
  iconContent: byHandle(),
  isRequest: false
};


class CreateEditRuleModal extends Component {
  constructor() {
    super();
    this.state = initialState;
  }

  advanceOpFun = (event) => {
    const eventVal = parseInt(event.target.value)
    if (eventVal === 1) {
      this.setState((prev) => ({ ...prev, selectedFeedsList: [], selectedNetworkList: [] }));
    } else if (eventVal === 2) {
      this.setState((prev) => ({ ...prev, selectedFeedsList: [] }));
    } else if (eventVal === 3) {
      this.setState((prev) => ({ ...prev, selectedNetworkList: [] }));
    }
    this.setState((prev) => ({
      ...prev,
      advanceOption: eventVal
    }));
    this.hideValidation();
  };

  handleOnChange = (event) => {
    const eventVal = event.target.value
    this.setState((prev) => ({
      ...prev,
      [event.target.name]: eventVal,
    }));
    this.hideValidation();
  };

  iconChangeOnTab = (event) => {
    const eventVal = parseInt(event.target.value)
    this.setState({
      ruleOn: eventVal
    })
    switch (eventVal) {
      case 1:
        return this.setState({ iconContent: byHandle() })
      case 2:
        return this.setState({ iconContent: byHashtag() })
      case 3:
        return this.setState({ iconContent: byWord() })
      case 4:
        return this.setState({ iconContent: byTag() })
      default:
        return this.setState({ iconContent: byHandle() })
    }
  };

  handleDropDownValue = (value) => {
    this.setState((prev) => ({ ...prev, action: value }));
    this.hideValidation();
  };


  handleChangeMultiSelect = (selectedOption) => {
    this.setState((prev) => ({ ...prev, tag: selectedOption }));
    this.hideValidation();
  };

  updateNetworkList = (id) => {
    const { selectedNetworkList } = this.state;

    if (selectedNetworkList.indexOf(id) !== -1) {
      var list = []
      if (id == 2) {
        selectedNetworkList.map((checkId) => {
          if (checkId != 2 && checkId != 18) list.push(checkId)
        })

      }
      else list = selectedNetworkList.filter((networkId) => networkId !== id);
      this.setState((prev) => ({ ...prev, selectedNetworkList: list }));
    } else {
      var networkId = [id]
      if (id == 2) {
        networkId = [2, 18]
      }
      this.setState((prev) => ({ ...prev, selectedNetworkList: [...this.state.selectedNetworkList, ...networkId] }));
    }
    this.hideValidation();
  };

  updateFeedList = (selectedFeedList) => {
    this.setState((prev) => ({ ...prev, selectedFeedsList: selectedFeedList }));
    this.hideValidation();
  };


  updateCollectionList = (selectedList) => {

    this.setState((prevState) => ({
      ...prevState,
      collectionId: selectedList.value,
    }));
    this.hideValidation();
  };

  convertArrToString = (list, isDestructingReq = false) => {
    if (list.length > 0 && !isDestructingReq) {
      const string = list.toString();
      return string;
    } else if (list.length > 0 && isDestructingReq) {
      const destructedArr = list.map((ele) => ele.value);
      return destructedArr.toString();
    }
    return "";
  };

  validatingAdvanceOption() {
    const { advanceOption, selectedNetworkList, selectedFeedsList } = this.state;
    if (advanceOption === 2 && selectedNetworkList.length === 0) {
      return true
    } else if (advanceOption === 3 && selectedFeedsList.length === 0) {
      return true
    }
    return false;
  }

  validatingAction() {
    const { ruleOn, text, tag } = this.state;

    if (ruleOn === 1 && text === '') {
      return true
    } else if (ruleOn === 2 && text === '') {
      return true
    } else if (ruleOn === 3 && text === '') {
      return true
    } else if (ruleOn === 4 && tag.length === 0) {
      return true
    }
    return false;
  }

  hideValidation() {
    const { validateAction, validateCollection, validateAdvOpt, validateName } = this.state;
    if (validateAdvOpt || validateName || validateAction || validateCollection) {
      this.setState((prev) => ({ ...prev, validateAdvOpt: false, validateName: false, validateAction: false, validateCollection: false }));
    }
  }

  handleCloseModal = () => {
    this.props.createEditCallback(false);
    this.setState(initialState);
  }

  handleFormSubmission = () => {
    const { name, collectionId } = this.state;
    if (name === "" || name.length <= 1) {
      this.setState((prev) => ({ ...prev, validateName: true }));
    } else if (collectionId === 0) {
      this.setState((prev) => ({ ...prev, validateCollection: true }));
    } else if (this.validatingAction()) {
      this.setState((prev) => ({ ...prev, validateAction: true }));
    } else if (this.validatingAdvanceOption()) {
      this.setState((prev) => ({ ...prev, validateAdvOpt: true }));
    } else {
      const { addNewRule } = this.props;
      const postbody = this.creatingPostBody();

      this.setState({ isRequest: true })
      addNewRule(postbody, this.handleCloseModal);
    }
  };

  creatingPostBody() {
    const { name, collectionId, advanceOption, action, text, ruleOn, selectedFeedsList, selectedNetworkList, tag } = this.state;

    const postbody = {
      name: name,
      collection: Number(collectionId),
      target: Number(advanceOption),
      action: Number(action === "Copy" ? "5" : "4"),
      text: ruleOn === 4 && tag.length > 0 ? this.convertArrToString(tag, true) : text,
      filter: Number(ruleOn),
      feeds: this.convertArrToString(selectedFeedsList),
      networks: this.convertArrToString(selectedNetworkList),
      tags: action !== "Copy" ? text : ''
    }
    if (ruleOn != 4 && action == "Tag") {
      postbody['tags'] = this.convertArrToString(tag, true);
    }
    if (postbody.action === 5) {
      postbody.tags = ''
    }
    return postbody;
  }

  render() {
    const { createEditToggle, networkList, tags } = this.props;
    const { ruleOn, name, text, action, advanceOption, selectedNetworkList, selectedFeedsList, validateAction, validateName, validateCollection, validateAdvOpt, iconContent, isRequest } = this.state;
    return (
      <Modal show={createEditToggle} onHide={() => this.handleCloseModal()} size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
        <Modal.Header closeButton>
          <Modal.Title>Add Rule</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
            <div className="row mb-4 mb-xl-5">
              <label className="col-sm-3 text-gray-800 mb-2">Rule Name <strong className="text-danger">*</strong></label>
              <div className="col-sm-9">
                <input type="text" name="name" className={`form-control ${validateName ? "is-invalid" : ""}`} placeholder="Enter rule nameâ€¦" value={name} onChange={this.handleOnChange} />
                <div className="invalid-feedback">Please provide a name.</div>
              </div>
            </div>
            <div className="row mb-4 mb-xl-5">
              <label className="col-sm-3 text-gray-800 mb-2">
                Select Collection <strong className="text-danger">*</strong>
              </label>
              <div className="col-sm-9">
                <div className={`w-100${validateCollection ? " is-invalid" : ""}`}>
                  <ChooseCollection className="w-100" updateCollectionList={this.updateCollectionList} onSelectedCollection={this.updateCollectionList} />
                </div>
                <div className="invalid-feedback">Please provide a name.</div>
              </div>
            </div>
            <div className="row mb-4 mb-xl-5">
              <label className="col-sm-3 text-gray-800 mb-2">Take Action <strong className="text-danger">*</strong></label>
              <div className="col-sm-9">
                <div className="d-flex align-items-center flex-wrap" onChange={this.iconChangeOnTab} >
                  <div className="form-check me-2 me-sm-3">
                    <input className="form-check-input" type="radio" name="ruleOn" value={1} id="byHandle" defaultChecked />
                    <label className="form-check-label" htmlFor="byHandle">By Handle</label>
                  </div>
                  <div className="form-check me-2 me-sm-3">
                    <input className="form-check-input" type="radio" name="ruleOn" value={2} id="byHashtag" />
                    <label className="form-check-label" htmlFor="byHashtag">By Hashtag</label>
                  </div>
                  <div className="form-check me-2 me-sm-3">
                    <input className="form-check-input" type="radio" name="ruleOn" value={3} id="byWord" />
                    <label className="form-check-label" htmlFor="byWord">By Word</label>
                  </div>
                  <div className="form-check">
                    <input className="form-check-input" type="radio" name="ruleOn" value={4} id="byTags" />
                    <label className="form-check-label" htmlFor="byTags">By Tags</label>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mb-4 mb-xl-5">
              <div className="offset-sm-3 col-sm-9">
                {ruleOn !== 4 ?
                  <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                    <span className="h-38px w-38px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex" style={{ zIndex: 9 }}>
                      {iconContent}
                    </span>
                    <InputGroup className={`${validateAction ? "is-invalid" : ""}`}>
                      <Form.Control className="ps-9 fw-bolder" aria-label="Text input with dropdown" name="text" placeholder="" onChange={(e) => this.handleOnChange(e)} value={text} />
                      <DropdownButton variant="outline-secondary" title={action}>
                        <Dropdown.Item onClick={() => this.handleDropDownValue("Copy")}>Copy</Dropdown.Item>
                        <Dropdown.Item onClick={() => this.handleDropDownValue("Tag")}>Tag</Dropdown.Item>
                      </DropdownButton>
                    </InputGroup>
                    <div className="invalid-feedback">Please provide a name.</div>
                  </div>
                  :
                  <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                    <div className={`${validateAction ? "is-invalid" : ""}`}>
                      <InputGroup className={`${validateAction ? "is-invalid" : ""}`}>
                        <CreatableSelect isMulti className="form-input-select selectpicker" classNamePrefix="selectpicker"
                          options={tags.tagList.map((tag) => ({ value: tag, label: tag }))} onChange={(event) => this.handleChangeMultiSelect(event)} />
                        <span className="input-group-text">Copy</span>
                      </InputGroup>
                    </div>
                    <div className="invalid-feedback">Please provide a name.</div>
                  </div>}
              </div>
            </div>
            {action == "Tag" ? <div className="row mb-4 mb-xl-5">
              <div className="offset-sm-3 col-sm-9">
                <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                  <div className={`${validateAction ? "is-invalid" : ""}`}>
                    <InputGroup className={`${validateAction ? "is-invalid" : ""}`}>
                      <CreatableSelect isMulti className="form-input-select selectpicker" classNamePrefix="selectpicker"
                        options={tags.tagList.map((tag) => ({ value: tag, label: tag }))} onChange={(event) => this.handleChangeMultiSelect(event)} />
                    </InputGroup>
                  </div>
                  <div className="invalid-feedback">Please provide a name.</div>
                </div>
              </div>
            </div> : null}
            <div className="row mb-4 mb-xl-5">
              <label className="col-sm-3 text-gray-800 mb-2 d-flex">
                Advance Options <strong className="text-danger">*</strong>
              </label>
              <div className="col-sm-9">
                <div className="d-flex align-items-center flex-wrap" onChange={this.advanceOpFun}>
                  <div className="form-check me-2 me-sm-3">
                    <input className="form-check-input" type="radio" name="advanceOpt" value={1} id="allFeedNetwork" defaultChecked />
                    <label className="form-check-label" htmlFor="allFeedNetwork">All Assets</label>
                  </div>
                  <div className="form-check me-2 me-sm-3">
                    <input className="form-check-input" type="radio" name="advanceOpt" value={2} id="selectedNetwork" />
                    <label className="form-check-label" htmlFor="selectedNetwork">Selected Networks</label>
                  </div>
                  <div className="form-check me-2 me-sm-3">
                    <input className="form-check-input" type="radio" name="advanceOpt" value={3} id="selectedFeed" />
                    <label className="form-check-label" htmlFor="selectedFeed">Selected Feeds</label>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mb-4 mb-xl-5">
              <div className="col-sm-12">
                <Collapse in={advanceOption === 2 ? true : false}>
                  <div>
                    <SelectedNetwork networkList={networkList} updateNetworkList={this.updateNetworkList} selectedField={selectedNetworkList} />
                  </div>
                </Collapse>
                <Collapse in={advanceOption === 3 ? true : false}>
                  <div>
                    <SelectedFeed updateFeedList={this.updateFeedList} selectedFeedsList={selectedFeedsList} />
                  </div>
                </Collapse>
              </div>
            </div>
            {validateAdvOpt ? <p>Please select a value</p> : null}
          </div>

        </Modal.Body>
        <Modal.Footer className="p-3">
          <div className="mx-xl-4 px-2 px-lg-4 mx-0">
            <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={() => this.handleCloseModal()}>Cancel</button>
            <button type="button" className={`btn btn-primary btn-sm btn-min-w ${isRequest ? `spinner` : ``}`} onClick={this.handleFormSubmission}>Save Rule</button>
          </div>
        </Modal.Footer>
      </Modal>
    );
  }
}
const mapStateToProps = state => {
  return {
    tags: state.tags
  }
}

const mapDispatchToProps = dispatch => ({
  addNewRule: (rule, callback) => dispatch(addRule(rule, callback))
})

export default connect(mapStateToProps, mapDispatchToProps)(CreateEditRuleModal);
